import React from 'react';
import '../../css/inicio.css';
const SectionContent = ({titulo1,titulo2,content1,content2,w1,w2,img1,img2,v,b,f1,f2})=>{
    return(
        <div class="columns is-desktop" style={{marginLeft:".75em"
        ,marginRight:".75em",padding:"3vh",border:b||""}}>
    <div class="column " style={{borderRadius:"2vh",textAlign:"center",width:w1,textAlign:"center",backdropFilter:"blur(10px)",background:f1}}>
        <img src={img1} style={{margin:"auto",display:v||"none"}}/>
        <h1 style={{fontSize:"3vh",display:"block",color:"white"}}>
            {titulo1}
        </h1>
        {content1} 
    </div>
    {"\u00a0"}{"\u00a0"}
    <div class="column " style={{width:w2,textAlign:"center",background:f2,borderRadius:"2vh"}}>
        <img src={img2} style={{margin:"auto",display:v||"none"}}/>
        <h1 style={{fontSize:"3vh",color:"white",textShadow:"2px 2px 1px black",fontFamily:"'Lobster'"}}>
            {titulo2}
        </h1>
        <hr class="hr" style={{width:"80%",margin:"auto",border:"2px solid white"}}/>
        <br/>
        <hr class="hr1" style={{width:"60%",margin:"auto",border:"2px solid white"}}/>
        <br/>
        <hr class="hr2"  style={{width:"40%",margin:"auto",border:"2px solid white"}}/>
        <br/>
        {content2}
    </div>
</div>
    );
}
export default SectionContent;