import React from 'react';
import pass from '../../medias/images/password.png';
import email from '../../medias/images/email.png';
import acad from '../../medias/images/academy (2).png';
const LogIn = ({w,color,som})=>{
    return(
        <>
        <form className="log" style={{boxShadow:som,borderRadius:"2em",padding:"1em"
          ,background:color
          ,width:w,height:"100%"
          ,margin:"auto"}}>
        <img class="fas" src={acad}/>
        <div class="field">
        <p class="control has-icons-left has-icons-right">
        <label class="label" style={{color:"white"}}>Nombre</label>
          <input class="input" type="email" placeholder="Email"/>
          <span class="icon is-small is-left">
            <img class="fas" src={email} style={{position:"absolute",bottom:"-23px"}}/>
          </span>
          <span class="icon is-small is-right">
            <i class="fas fa-check"></i>
          </span>
        </p>
      </div>
      <div class="field">
        <p class="control has-icons-left">
        <label class="label" style={{color:"white"}}>Contraseña</label>
          <input class="input" type="password" placeholder="Password"/>
          <span class="icon is-small is-left">
          <img src={pass} style={{position:"absolute",bottom:"-20px"}}/>
          </span>
        </p>
      </div>
      <div class="field">
        <p class="control">
          <button class="button  is-danger is-fullwidth is-outlined">
            Entrar
          </button>
        </p>
      </div>
      </form>
      </>
    );
}
export default LogIn;