import React from 'react';

const TituloApartados = ({titulo,img,mar})=>{
    return(
        <div style={{background:"rgb(255,255,255,.5)"
        ,paddingTop:"5vh"
        ,paddingBottom:"5vh"
        ,marginLeft:mar
        ,marginRight:mar}}>
        <img src={img} />
        {"\u00a0"}
        <p class="title" style={{color:"white"}}>{titulo}</p>
    </div>
    );
}
export default TituloApartados;