import React from 'react';

const Fondo =({fondo,mensaje,tam,image})=>{
    return(
        <div class="columns is-desktop"
        style={{marginLeft:tam
        ,marginRight:tam
        ,borderTop:"3px solid black"}}
        >
            <div class="column"
                style={{backgroundImage:`url(${fondo})`
                ,width:"100%"
                ,backgroundAttachment:"fixed"
                ,backgroundSize:"cover"
                ,position:"relative"
                }}>
                    <img class="is-primary" style={{position:"absolute",top:"-20%",width:"6em"
                        ,borderRadius:"100%"
                        ,border:"6px solid #00d1b2"
                        ,margin:"auto"
                        ,right:"0",left:"0",background:"#ff2e93",boxShadow:""}} src={image}>
                    </img>
                    <p className="title an" style={{color:"white",fontSize:"1.6em"}}>
                    {mensaje}
                    </p>
            </div>
        </div>
    );
}
export default Fondo;