import React from 'react';

const TextoImage = ({img1,text1,img2,text2})=>{
    return(
<div class="columns is-desktop" style={{position:"relative",marginLeft:".75em",marginRight:".75em"}}>

  <div class="column ty" style={{position:"relative",height:"20em"}}>
        <div class="columns is-mobile" style={{position:"absolute",top:"0",left:"0",width:"100%"}}>
            <div class="column">
                <img src={img1}/>
            </div>
            <div class="column til" style={{fontFamily:"fantasy"}}>
               {text1}
            </div>
        </div>
  </div>

  <div class="column ty" style={{position:"relative",height:"20em"}}>
        <div class="columns is-mobile" style={{position:"absolute",bottom:"0",right:"0",width:"100%"}}>
            <div class="column til"style={{fontFamily:"fantasy",position:"relative"}}>
            <p style={{bottom:"0" ,position:"absolute"}}>{text2}</p>
            </div>
            <div class="column">
                <img src={img2}/>
                
            </div>
        </div>
  </div>

</div>
    );
}
export default TextoImage;