import React from 'react';
import image from '../../medias/images/cyborg-88.png';
import '../../css/title.css';
import {useAnimatorLoad} from '../../hooks/useAnimator';
import Nubes from '../section/Nubes';
const Header = ()=>{
    useAnimatorLoad(".anm")
    return(
        <section class="hero is-primary" style={{}}>
            <br/>
            <div class="hero-body">
                <div class="columns is-desktop">
                    <div class="column">
                        <p class="title anm fadekey" style={{fontFamily: "'Press Start 2P', cursive"}}>
                            Bienvenido a MS Academy una plataforma donde aprendera desarrollo web fullstack de forma Gratuita
                        </p>
                
                        <br/>
                        <p class="subtitle">
                            <div class="control">
                                <div class="tags has-addons">
                                    <a class="tag is-link">Maynor Alvarez Acopa</a>
                                    <a class="tag">
                                        <img src="https://img.icons8.com/plasticine/20/000000/bookmark--v1.png" />
                                    </a>
                                </div>
                            </div>
                        </p>
                    </div>
                    <div class="column" style={{textAlign:"center"}}>
                        <img className="_image" src={image} style={{width:"45%",transform:"scale(2)"}}/>
                    </div>
                </div>
                <Nubes top="33%" left="20%" scale=".2" />
                <Nubes top="33%" left="28%" scale=".2" top="30%" />
                <Nubes top="33%" left="35%" scale=".2" top="20%" />
                <Nubes top="33%" left="2%" scale=".6"/>
            </div>
           
        </section>
    );
}
export default Header;