import React from 'react';
import '../../css/inicio.css';
import Fondo from './Fondo';
import fondo from '../../medias/images/desktop-1245714.jpg';
import circulo from '../../medias/images/circled.png';
import SectionContent from './SectionContent';
import GridCards from './GridCards';
import {targeta} from '../../Api/inicioCards';
import Login from './LogIn';
import Nota from '../../medias/images/content.png';
import Titulo from './TituloApartados';
import TextImage from './TextoImage';
import ilustracion1 from '../../medias/images/ilustracion1s.png';
import ilustracion2 from '../../medias/images/ilustracion2s.png';
import ilustracion3 from '../../medias/images/ilustracion3s.png';
import ilustracion4 from '../../medias/images/ilustracion4s.png';
import ilustracion5 from '../../medias/images/ilustracion5s.png';
import ilustracion6 from '../../medias/images/ilustracion6s.png';
import ilustracion7 from '../../medias/images/ilustracion10s.png';

const Section = ()=>{
    //useAnimator(".an",-960);
    return(
        <>
            <section class="hero is-primary" style={{backgroundAttachment:"fixed"}}>
            <br/>
            <br/>
            <br/>
            <Fondo fondo={fondo} 
                    image={circulo} 
                    mensaje={<><br/>
                            A continuación encontraras...<br/>
                            {"\u00a0"}{"\u00a0"}los diferentes cursos que ofrecemos<br/>
                            {"\u00a0"}{"\u00a0"}{"\u00a0"}Algunos cursos dependen de otros por lo que sera necesario<br/>
                            {"\u00a0"}{"\u00a0"}{"\u00a0"}{"\u00a0"}cursar el correspondiente para poder avanzar<br/><br/> </>} 
                    tam="0">
            </Fondo>
            <SectionContent 
                w2="" 
                titulo2="Contenido del curso" 
                content2={<GridCards targeta={targeta}/>}
                content1={<Login w="50%"/>}
                f1="rgb(255,255,255,.5)" f2="rgb(255,255,255,.5)"
                img2={Nota}
                v="block">
            </SectionContent>
            <Titulo/>
            <br/>
            <TextImage 
                text1={<>Contenido especializado para que lo puedas entender facilmente</>}
                text2={<>Con acceso de por vida<br/> sin ningun tipo de costo</>} 
                img1={ilustracion1} 
                img2={ilustracion2}>
            </TextImage>
            <TextImage 
                text1={<>conviértete en un desarrollador java fullstack</>}
                text2={<>Logra tus metas desde internet</>} 
                img1={ilustracion3} 
                img2={ilustracion7}>
            </TextImage>
            <TextImage 
                text1={<>recuera que: "el que tiene fé en si mismo no necesita que los demas crean en el"<br/>Miguel de unamundo</>}
                text2={<>Estas listo para comenzar?</>} 
                img1={ilustracion5} 
                img2={ilustracion6}>
            </TextImage>
            <br/>
             <br/>
             <br/>
            </section>
        </>
    );
}
export default Section;